<template>
  <div>
      <!-- 这是关联用户 -->
      <div class="relationBox_b">
          <div class="jgTab">
            <div class="jieGou">
                <div class="jieGou_tit">组织结构</div>
                <el-input
                placeholder="输入关键字进行过滤"
                v-model="filterText">
              </el-input>
              <el-tree
                class="filter-tree"
                :data="data"
                @node-click="handleNodeClick"
                :props="defaultProps"
                default-expand-all
                :filter-node-method="filterNode"
                ref="tree">
              </el-tree>
            </div>
            <!-- 这是绑定的人员数据表 -->
            <div class="tablebox">
              <div class="jieGou_tit">绑定用户</div>
              <el-table
                :data="tableDatareRE"
                stripe
                style="width: 100%">
                <el-table-column
                  prop="date"
                  label="用户名"
                  width="180">
                </el-table-column>
                <el-table-column
                  prop="name"
                  label="姓名"
                  width="180">
                </el-table-column>
                <el-table-column
                  prop="address"
                  label="手机">
                </el-table-column>
                <el-table-column
                  prop="address"
                  label="操作">
                </el-table-column>
              </el-table>
            </div>
          </div>
      </div>
  </div>
</template>

<script>
export default {
    data(){
        return{
            filterText: '',//关联用户搜素狂输入
            data: [{//作者是等级列表
            id: 1,
            label: '一级 1',
            children: [{
                id: 4,
                label: '二级 1-1',
                children: [{
                id: 9,
                label: '三级 1-1-1'
                }, {
                id: 10,
                label: '三级 1-1-2'
                }]
            }]
            }, {
            id: 2,
            label: '一级 2',
            children: [{
                id: 5,
                label: '二级 2-1'
            }, {
                id: 6,
                label: '二级 2-2'
            }]
            }, {
            id: 3,
            label: '一级 3',
            children: [{
                id: 7,
                label: '二级 3-1'
            }, {
                id: 8,
                label: '王小二'
            }]
            }],
            defaultProps: {//这是几级定义
            children: 'children',
            label: 'label'
            },
            tableDatareRE:[]//这是绑定的人员数据表
        };
    },
    methods:{
      filterNode(value, data) {//这是关联用户筛选出符合条件的数据
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
      },
      handleNodeClick(data) {//这是树状图的点击节点事件
        console.log(data,'-----------树状图的点击节点事件');
      }
    },
    watch: {
      filterText(val) {
        this.$refs.tree.filter(val);
      },
    },
}
</script>

<style lang="stylus" scoped>
.relationBox_b
  padding 30px
  .jgTab
    display flex
    .jieGou
      width 300px
    .jieGou_tit
      margin-bottom 17px
    .tablebox
      flex 1
      margin-left 30px
</style>


